<template>
	<section class="sidebar">
		<section class="section-title">
			<h2 class="section-title__text">Панель</h2>
		</section>
		<ul class="sidebar-list" @click='showTab' v-for='tab in tabs' :key='tab.title'>
			<li class="sidebar-list__item" :data-tab='tab.dataAttr'>{{tab.title}}</li>
		</ul>
	</section>
</template>
<script>
	export default {
		name: 'AdminSidebar',
		data() {
			return {
				tabs: [
					{
						dataAttr: 'personal',
						title: 'Персональна інформація'
					},
					{
						dataAttr: 'posts',
						title: 'Блог'
					},
					{
						dataAttr: 'works',
						title: 'Портфоліо'
					},
					{
						dataAttr: 'services',
						title: 'Послуги'
					},
					{
						dataAttr: 'drafts',
						title: 'Чернетки'
					}
				]
			}
		},
		methods: {
			showTab(e) {
				const target = e.target;
				if (target.tagName === 'LI') {
					const value = target.dataset.tab;
					this.$store.commit('changeTab', value);
				}
			}
		}
	}
</script>
<style lang='sass'>
	@import '~@/assets/sass/_vars'

	.sidebar
		position: sticky
		top: 100px
		&-list
			padding-left: 0
			list-style: none
			&__item
				cursor: pointer
				font-family: $ff
				padding: 10px
				font-size: 16px
				line-height: 24px
				font-weight: 400
				text-transform: uppercase
				letter-spacing: 0.22px
				color: $primary-text
				border: 1px dotted $primary-text

</style>